<template>
  <div id='template'>
  </div>
</template>

<script>
const d3 = require('d3')
export default {
  mounted: function () {
    (function () {
    // set the dimensions and margins of the graph
      var margin = {top: 20, right: 10, bottom: 30, left: 40}
      var w = 960 - margin.left - margin.right
      var h = 600 - margin.top - margin.bottom
      // parse the date / time
      var parseTime = d3.timeParse('%d-%b-%y')
      // set the ranges
      var x = d3.scaleTime().range([0, w])
      var y = d3.scaleLinear().range([h - margin.top, 0])
        .rangeRound([h, 0])
        .nice()
      /*
    var area = d3.area()
      .x(function (d) { return x(d.date) })
      .y0(h)
      .y1(function (d) { return y(d.plat) })
   */
      // var dataWeeks = d3.map()
      // var dataDays = d3.map()
      const div = d3.select('body').append('div')
        .attr('class', 'tooltip')
        .style('opacity', 0)
      // Show min max
      // eslint-disable-next-line no-unused-vars
      function showMinMax (ds, col, val, type) {
        var max = d3.max(ds, function (d) { return d[col] })
        var min = d3.min(ds, function (d) { return d[col] })
        if (type === 'minmax' && (val === max || val === min)) {
          return val
        } else {
          if (type === 'all') {
            return val
          }
        }
      }
      //
      var svg = d3.select('#template').append('svg')
        .attr('viewBox', [0, 0, (w + margin.left + margin.right), (h + margin.top + margin.bottom)])
      // .attr('preserveAspectRatio', 'xMinYMin meet')
      // .attr('viewBox', '0 0 ' + (w + margin.left + margin.right) + ' ' + (h + margin.top + margin.bottom))
        .append('g')
        .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')')
      // Add Grid lines
      /* gridlines in y axis function
    function makeYGridlines () {
      return d3.axisLeft(y)
        .ticks(5)
    } */
      d3.queue()
        .defer(d3.csv, 'static/data/template.csv')
        .defer(d3.csv, 'static/data/template1.csv')
      // eslint-disable-next-line handle-callback-err
        .await(function (error, dataW, dataD) {
          console.log(dataW) // [ ...dataWeeks.keys() ]
          if (error) throw error
          dataW.forEach(function (d) {
            d.date = parseTime(d.date)
            d.plat = +d.plat
          })
          var newarr = dataW.filter(function (d) {
            return !(d.date === null) || !(isNaN(d.plat))
          })
          dataD.forEach(function (d) {
            d.date = parseTime(d.date)
            d.case = +d.case
          })
          var anotherArr = dataD.filter(function (d) {
            return !(d.date === null) || !(isNaN(d.case))
          })
          // const newArray = newarr.map(a => ({...a}))
          // newArray.reverse()

          // var wAvg = weeksAvg(newArray)
          // console.log(newarr)
          // console.log(dataDays.get(d.date))
          // Scale the range of the data
          x.domain(d3.extent(newarr, function (d) { return d.date }))
          y.domain(d3.extent(anotherArr, function (d) { return d.case }))
          // Add the paths with different curves.
          // svg.append('path')
          // .datum(newarr)
          // .attr('class', 'area')
          // .attr('d', area)
          svg.append('path')
            .datum(newarr)
            .attr('class', 'line')
            .attr('d', d3.line()
              .curve(d3.curveCardinal)
              .x(function (d) { return x(d.date) })
              .y(function (d) { return y(d.plat) })
            )
          /*
        svg.selectAll('circle')
          .data(newarr)
          .enter()
          .append('circle')
          .attr('r', 5)
          .attr('cx', function (d) { return x(d.date) })
          .attr('cy', function (d) { return y(d.plat) })
          .style('stroke', 'blue') // set the line colour
          .style('fill', 'none')
          .style('stroke-width', '2px') */
          /*
          .on('mouseover', function (d) {
            div.transition()
              .duration(200)
              .style('opacity', 0.9)
            div.html(d.plat)
              .style('left', (d3.event.pageX + 5) + 'px')
              .style('top', (d3.event.pageY - 25) + 'px')
          })
          .on('mouseout', function (d) {
            div.transition()
              .duration(500)
              .style('opacity', 0)
          }) */
          // Add the X Axis
          svg.append('g')
          // .attr('class', 'axis')
            .attr('transform', 'translate(0,' + h + ')')
          // .call(d3.axisBottom(x))
            .call(d3.axisBottom(x).tickSizeOuter(0))
          // Add the Y Axis
          svg.append('g')
          // .attr('class', 'axis')
          // .call(d3.axisLeft(y).ticks(null, 's'))
            .attr('transform', 'translate(0,' + margin.top - 10 + ')')
          // .attr('class', 'axis')
          // .style('opacity', 0.7)
            .call(
              d3.axisLeft(y)
                .tickSizeOuter(0)
                .ticks(4)
            )
          /*
      svg.append('text')
        .attr('x', w / 4)
        .attr('y', 0 + (margin.top + 40) / 2)
        .attr('text-anchor', 'middle')
        .style('font', '24px cabin')
        .style('text-decoration', 'underline')
        .text('تسطيح منحنى الوباء بالمغرب') */
          svg.append('text')
            .attr('x', 100)
            .attr('y', 0 + (margin.top + 360) / 2)
          // .attr('text-anchor', 'middle')
            .style('font', '24px cabin')
            .style('opacity', '0.6')
          // .style('text-decoration', 'underline')
            .text('بلغ عدد الحالات الاجمالية 24322 اواخر شهر يوليوز')
          svg.append('text')
            .attr('x', 205)
            .attr('y', 0 + (margin.top + 420) / 2)
          // .attr('text-anchor', 'middle')
            .style('font', '24px cabin')
            .style('opacity', '0.6')
          // .style('text-decoration', 'underline')
            .text('ما يعني 675 حالة ل: 1,000.000 نسمة')
          svg.append('text')
            .attr('x', 150)
            .attr('y', 0 + (margin.top + 480) / 2)
          // .attr('text-anchor', 'middle')
            .style('font', '24px cabin')
            .style('opacity', '0.6')
          // .style('text-decoration', 'underline')
            .text('و بلغ عدد الحالات الاسبوع الماضي 5488 حالة')
          svg.append('text')
            .attr('x', 640)
            .attr('y', 54)
          // .attr('text-anchor', 'middle')
            .style('font', '24px cabin')
            .style('opacity', '0.7')
          // .style('text-decoration', 'underline')
            .text('31/07/2020')
          svg.append('svg:defs').append('svg:marker')
            .attr('id', 'triangle')
            .attr('refX', 6)
            .attr('refY', 6)
            .attr('markerWidth', 30)
            .attr('markerHeight', 30)
            .attr('markerUnits', 'userSpaceOnUse')
            .attr('orient', 'auto')
            .append('path')
            .attr('d', 'M 0 0 12 6 0 12 3 6')
            .style('fill', 'black')
          // line
          svg.append('line')
            .attr('x1', 780)
            .attr('y1', 50)
            .attr('x2', 910)
            .attr('y2', 50)

            .attr('stroke-width', 2)
            .attr('stroke', 'black')
            .attr('marker-end', 'url(#triangle)')

          svg.append('text')
            .attr('x', 600)
            .attr('y', 84)
          // .attr('text-anchor', 'middle')
            .style('font', '24px cabin')
            .style('opacity', '0.7')
          // .style('text-decoration', 'underline')
            .text('عدد الحالات: 1063')
          /* add the Y gridlines
        svg.append('g')
          .attr('class', 'grid')
          .style('opacity', '0.1')
          .style('stroke-dasharray', ('3,3'))
          .call(makeYGridlines()
            .tickSize(-w)
            .tickFormat('')
          ) */
          /*  */

          // console.log(anotherArr)
          // var group = svg.append('g')
          svg.selectAll('.bar')
            .data(anotherArr) // 2
            .enter() // 3
            .append('rect') // 4
            .attr('class', 'bar')
            .attr('x', d => x(d.date)) // 5
            .attr('y', d => y(d.case)) // 6
            .attr('width', 5) // 7 .attr('height', d => h - y(d.case))   showMinMax (ds, col, val, type)
            .attr('height', d => h - y(d.case))
            .style('opacity', 0.3)
            .on('mouseover', function (d) {
              div.transition()
                .duration(200)
                .style('opacity', 0.9)
              div.html(d.case)
                .style('left', (d3.event.pageX - 10) + 'px')
                .style('top', (d3.event.pageY - 25) + 'px')
            })
            .on('mouseout', function (d) {
              div.transition()
                .duration(500)
                .style('opacity', 0)
            })
        })
    })()
  }
}
</script>
<style>
.line {
  fill: none;
  stroke: rgb(245, 5, 5);
  stroke-width: 4px;
}
.legend {
    font-size: 14px;
    font-weight: bold;
    text-anchor: left;
}
#svg {
    display: block;
    margin: auto;
}

.bar {
  fill: MidnightBlue;
}

.bar:hover {
  fill: #23415A;
}

.area {
  fill: none;
}
text.shadow {
    stroke: white;
    stroke-width: 4px;
    opacity: 1
}

.grid {
  fill: none;
}
.grid line {
    fill: none;
    shape-rendering: crispedges;
    stroke: #e4e4e4;
}
.grid path {
    stroke-width: 0;
}

.tooltip {
position: absolute;
opacity:0.8;
z-index:1000;
text-align:left;
border-radius:20px;
-moz-border-radius:20px;
-webkit-border-radius:20px;
padding:2px;
color:rgb(204, 5, 5);
/*background-color:#000;*/
font: 8px sans-serif;
max-width: 200px;
font-weight: bold;
}
</style>



