<script>

import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  data () {
    return {
      datacollection: {
        labels: [
          '25/يوليوز',
          '26/يوليوز',
          '27/يوليوز',
          '28/يوليوز',
          '29/يوليوز',
          '30/يوليوز',
          '31/يوليوز'
        ],
        datasets: [
          /* {
            label: 'عدد الاختبارات',
            backgroundColor: '#d8b365',
            borderWidth: 1,
            fill: false,
            data: [
              16025,
              18505,
              18481,
              16581,
              16431,
              30034,
              13059,
              12701,
              16381,
              18143,
              16834,
              17392,
              16322,
              15421
            ]
          }, */
          {
            label: 'حالات مؤكدة',
            backgroundColor: 'blue',
            borderWidth: 1,
            fill: false,
            data: [
              811,
              633,
              609,
              500,
              826,
              1046,
              1063
            ]
          }
        ]
      },
      options: {
        datasets: { bar: { categoryPercentage: 0.6, barPercentage: 0.6 } },
        elements: { line: { fill: false },
          point: {
            radius: 0
          }},
        tooltips: {
          mode: 'index',
          intersect: false
        },
        hover: {
          bodyalign: 'right',
          mode: 'nearest',
          intersect: true
        },
        scales: {
          yAxes: [{
            display: true,
            position: 'right',
            stacked: true,
            scaleLabel: {
              display: true,
              labelString: 'عدد الحالات'
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [{
            display: true,
            stacked: true,
            scaleLabel: {
              display: false,
              labelString: 'التاريخ'
            },
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: false
        },
        responsive: true,
        maintainAspectRatio: false,
        layout: {
          padding: {
            left: 6,
            right: 6,
            top: 4,
            bottom: 6
          }
        }
      }
    }
  },
  mounted () {
    this.renderChart(this.datacollection, this.options)
  }
}
</script>



